import {
	mapState
} from 'vuex';
import uniNumberBox from '@/components/uni-number-box.vue'
import uniRequest from 'uni-request'
export default {
	components: {
		uniNumberBox
	},
	data() {
		return {
			total: 0, //总价格
			disTotal:0,
			allChecked: false, //全选状态  true|false
			empty: false, //空白页现实  true|false
			cartList: [],
			token:'',
			code:true,
		};
	},
	onLoad(){
	},
	onShow(){
		this.loadData();
		if(uni.getStorageSync('userInfo').code !== ''){
			this.code = true
		}else{
			this.code = false
		}
	},
	watch:{
		//显示空白页
		cartList(e){
			let empty = e.length === 0 ? true: false;
			if(this.empty !== empty){
				this.empty = empty;
			}
		}
	},
	computed:{
		...mapState(['hasLogin','login'])
	},
	methods: {
		//请求数据
		async loadData(){
			const response = await uniRequest({
				url:'/cart/',
				method:'get',
				headers:{
					Authorization:'JWT '+uni.getStorageSync('userInfo').token
				},
				crossDomain: true,
				xhrFields: {
					withCredentials: true
				},
			}).then(res=>{
				console.log(res.data)
				let cartList = res.data.map(item=>{
					return item;
				});
				this.cartList = cartList;
				this.calcTotal();  //计算总价
			}).catch({
				
			})
			
		},
		//监听image加载完成
		onImageLoad(key, index) {
			this.$set(this[key][index], 'loaded', 'loaded');
		},
		//监听image加载失败
		onImageError(key, index) {
			this[key][index].image = '/static/errorImage.jpg';
		},
		gotoPath(id){
			uni.navigateTo({
				url: '/pages/product/product?id='+id
			})
		},
		navToLogin(){
			uni.navigateTo({
				url: '/pages/public/login'
			})
		},
		 //选中状态处理
		async check(type, index){
			if(type === 'item'){
				const response = await uniRequest({
					url:'/cart/',
					method:'put',
					data:{
						sku_id: this.cartList[index].id,
						count: this.cartList[index].count,
						selected: !this.cartList[index].selected
					},
					headers:{
						Authorization:'JWT '+uni.getStorageSync('userInfo').token
					},
				})
				this.cartList[index].selected = !this.cartList[index].selected;
			}else{
				const selected = !this.allChecked
				const list = this.cartList;
				list.forEach(item=>{
					item.selected = selected;
				})
				this.allChecked = selected;
			}
			this.calcTotal(type);
		},
		//数量
		numberChange(data){
			console.log(data)
			this.cartList[data.index].count = data.number;
			this.calcTotal();
		},
		//删除
		deleteCartItem(index){
			let list = this.cartList;
			let row = list[index];
			let id = row.id;
			uni.showModal({
				content: '删除当前商品？',
				success: async (e)=>{
					if(e.confirm){
						const response = await uniRequest({
							url:'/cart/',
							method:'delete',
							data:{sku_id:id},
							headers:{
								Authorization:'JWT '+uni.getStorageSync('userInfo').token
							},
						})
						this.cartList.splice(index, 1);
						this.$api.msg(`删除 `+row.name+`成功`);
						this.calcTotal();
						uni.hideLoading();
					}
				}
			})
		},
		//清空
		clearCart(){
			uni.showModal({
				content: '清空购物车？',
				success:async (e)=>{
					if(e.confirm){
						const response = await uniRequest({
							url:'/cart/select/',
							method:'delete',
							headers:{
								Authorization:'JWT '+uni.getStorageSync('userInfo').token
							},
						})
						this.cartList = [];
						this.$api.msg(`全部商品删除成功`);
					}
				}
			})
		},
		//计算总价
		calcTotal(){
			let list = this.cartList;
			if(list.length === 0){
				this.empty = true;
				return;
			}
			let total = 0;
			let disTotal = 0
			let selected = true;
			list.forEach(item=>{
				if(item.selected === true){
					total += item.price * item.count;
					disTotal += item.dis_price * item.count;
				}else if(selected === true){
					selected = false;
				}
			})
			this.allChecked = selected;
			this.total = Number(total+'').toFixed(2);
			console.log(total,disTotal)
			this.disTotal = (+total - +disTotal);
			console.log(this.disTotal)
		},
		//创建订单
		createOrder(){
			let list = this.cartList;
			let goodsData = [];
			list.forEach(item=>{
				if(item.selected){
					goodsData.push(item)
				}
			})
			uni.navigateTo({
				url: `/pages/order/createOrder?data=`+JSON.stringify(goodsData)
			})
		}
	}
}
